export default {
  name : "Home",
  async asyncData({ $axios }) {
  const temp = await $axios.$get('https://jsonplaceholder.typicode.com/posts')
  
  return { temp }
  },
  data (){
    return {
      posts : [],
      temp : [],
      loading : true
    }
  },
  
  methods : {
    showPosts(){
      for(let i = 0 ; i<=5;i++){
            this.posts.push(this.temp[i]);
            if(i==5) return this.loading = false
          }

    }

},
created () {
  this.showPosts()
}
}
